// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import axios from 'axios'
import VueRouter from 'vue-router'
import router from './routers'
import Vuetify from 'vuetify'
import Croppa from 'vue-croppa'
import 'vuetify/dist/vuetify.min.css'
import 'material-design-icons-iconfont/dist/material-design-icons.css'
import 'vue-croppa/dist/vue-croppa.css'

axios.defaults.baseURL = 'http://192.168.1.107:8080/';
//axios.defaults.baseURL = '/api';
axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.headers.common['Access-Control-Allow-Origin'] = '192.168.1.107:8080';
axios.defaults.headers.common['Access-Control-Allow-Methods'] = 'POST, GET, DELETE, OPTIONS, PUT';
axios.defaults.withCredentials = true;

Vue.config.productionTip = false;
Vue.use(Vuetify);
Vue.use(VueRouter);
Vue.use(Croppa);
Vue.prototype.$http = axios;

new Vue({
  el: '#app',
  router,
  render: h => h(App)
});



