<template>
  <v-app id="inspire">
    <v-navigation-drawer fixed v-model="drawer" disable-route-watcher temporary app>
      <v-card flat tile>
        <v-card-media src="/static/drawerBG.jpg" height="150px">
          <v-container fluid>
            <v-layout row>
              <v-flex xs4 offset-xs4>
                <v-avatar slot="activator" size="72px">
                  <img src="https://avatars0.githubusercontent.com/u/9064066?v=4&s=460" alt="">
                </v-avatar>
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex xs4 offset-xs4>
                <p class="headline white--text"><strong>{{adminName}}</strong></p>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-media>
        <v-card-text>
          <v-list dense class="pt-0">
            <v-list-tile v-for="item in items" :key="item.text" :to="item.path" @click="drawer=false">
              <v-list-tile-action>
                <v-icon>{{item.icon}}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title><span class="subheading">{{item.text}}</span></v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider></v-divider>
            <v-list-tile to="/admin/info" @click="drawer=false">
              <v-list-tile-action>
                <v-icon>create</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title><span class="subheading">修改信息</span></v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile @click="">
              <v-list-tile-action>
                <v-icon>exit_to_app</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title><span class="subheading">退出登录</span></v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card-text>
      </v-card>
    </v-navigation-drawer>
    <v-toolbar :color="color" dark fixed app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>AlphaCat后台管理系统</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon>exit_to_app</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content>
      <router-view></router-view>
    </v-content>
    <v-footer :color="color" app inset>
      <v-flex xs12>
        <p class="white--text text-xs-center">南京大学软件学院AlphaCat开发小组&copy; 2017-2018</p>
      </v-flex>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    name: "AdminPage",
    data() {
        return {
          drawer: false,
          color: 'indigo',
          items: '',
          snackBar: false,
          timeout:2000,
          snackBarText: '',
          adminName: sessionStorage.getItem('name')
        }
    },
    methods: {
    },
    mounted() {
      switch (sessionStorage.getItem('type')) {
        case 'superAdmin':
          this.items = [
            {icon: 'people', text: '管理员设置', path: '/admin/index'},
            {icon: 'poll', text: '用户数据统计', path: ''},
            {icon: 'timeline', text: '任务数据统计', path: ''},
            {icon: 'library_books', text: '通知管理', path: '/admin/notice'},
            //{icon: '', text: '', path: ''},
          ];
          break;
        case 'requesterAdmin':
          this.items = [
            {icon: 'people', text: '发起者管理', path: '/admin/index'},
            {icon: 'gavel', text: '发起者审核', path: '/admin/check'},
            {icon: 'poll', text: '发起者数据统计', path: ''}
          ];
          break;
        case 'workerAdmin':
          this.items = [
            {icon: 'people', text: '工人信息管理', path: '/admin/index'},
            {icon: 'poll', text: '工人数据统计', path: ''}
          ];
          break;
      }
    }
  }
</script>

<style scoped>

</style>



