<template>
  <v-app id="worker_login">
    <v-toolbar id="toolbar" color="transparent" dark app flat>
      <v-spacer></v-spacer>
      <v-toolbar-items>
      <v-btn color="transparent" depressed class="head_buttons" to="/">
        <v-icon dark left>reply</v-icon>
        返回首页
      </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-jumbotron src="/static/light_train.jpg" dark height="200px" :gradient="gradient">
      <v-container fill-height>
      <v-layout align-center class="text-lg-left">
        <v-flex>
          <h3 class="display-2">{{title}}</h3>
          <span class="subheading">{{sub_title}}</span>
        </v-flex>
      </v-layout>
      </v-container>
    </v-jumbotron>
    <v-content id="content">
        <v-container>
          <v-layout align-center class="text-lg-left">
            <v-flex xs12 sm7>
              <div class=" headline  mb-2 " id="welcome">{{welcome}}</div>
              <em class="grey--text">注意阅读右方提示信息噢</em>
              <v-divider  class="my-3"></v-divider>
              <v-card>
                <v-card-text>
                  <v-form :model="loginData">
                    <v-text-field v-model="loginData.name" color="blue-grey darken-2" prepend-icon="person" label="用户名" type="text"></v-text-field>
                    <v-text-field v-model="loginData.key" color="blue-grey darken-2" prepend-icon="lock" label="密码" id="password" type="password"></v-text-field>
                    <v-text-field color="blue-grey darken-2" prepend-icon="vpn_key" label="验证码" type="text"></v-text-field>
                  </v-form>
                  <em>忘记密码？</em>
                  <a href="#">点我找回</a>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn dark color="indigo darken-3" @click="login">登录</v-btn>
                </v-card-actions>
              </v-card>
            </v-flex>
            <v-flex xs12 sm6 offset-sm1>
              <v-card class="elevation-0 transparent">
                <v-card-title primary-title class="layout ">
                  <div class="headline" id="tip">小贴士</div>
                </v-card-title>
                <v-list class="transparent">
                  <template v-for="(item, index) in items">
                    <v-list-tile v-if="item.action" :key="item.title">
                      <v-list-tile-action>
                        <v-icon color="indigo">{{ item.action }}</v-icon>
                      </v-list-tile-action>
                      <v-list-tile-content >
                        <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                      </v-list-tile-content>
                    </v-list-tile>
                  </template>
                </v-list>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      <br/>
      <br/>
      <v-footer class="indigo darken-3" id="footer">
        <v-layout row wrap align-center>
          <v-flex xs12 text-xs-center>
            <div class="white--text ml-3">
              Made with
              <v-icon class="red--text">favorite</v-icon>
              by AlphaCat Team
            </div>
          </v-flex>
        </v-layout>
      </v-footer>
    </v-content>
  </v-app>
</template>

<script>
  export default {
    name: "WorkerLogin",
    data() {
        return {
          gradient: 'to top right, rgba(63,81,181, .3), rgba(25,32,72, .3)',
          title: '众包标注平台',
          sub_title: 'Crowdsourcing Tags',
          welcome: 'Hi, 亲爱的标注工人, 欢迎登录~',
          items: [
            {
              action: 'label',
              title: '每日签到可增加经验值哦'
            },
            {
              action: 'label',
              title: '记得查看系统邮件，说不定有惊喜呢'
            },
            {
              action: 'label',
              title: '如遇到问题请联系我们，管理员会尽快给您反馈'
            }
          ],
          loginData : {
            name:'',
            key:'',
            type: 'worker',
          },
        }
      },
    methods: {
        login() {
          this.$http.post('/login', this.loginData)
            .then((response) => {
              let result = response.data.result;
              if (result === 'success') {
                sessionStorage.setItem('id', response.data.id);
                sessionStorage.setItem('name', response.data.name);
                sessionStorage.setItem('type', response.data.type);
                this.$router.push("/worker/task");
              } else if (result === 'failed') {
                this.$message.warning(response.data.info);
              }
            })
            .catch(function (error) {
              console.log(error);
            });
        },
      }
    }
</script>

<style scoped>
  .head_buttons{
    font-size: larger;
  }
  #welcome{
    color: #616161;
    text-shadow: 1px 1px 1px #7986CB;
  }
  #tip{
    color: #616161;
    text-shadow: 1px 1px 1px #7986CB;
  }
  #footer{
    opacity: 0.8;
  }
</style>



