<template>
  <v-container>
    <v-layout row>
      <v-flex xs12>
        <v-card>
          <v-card-title>
            <v-list>
              <v-list-tile>
                <v-list-tile-avatar>
                  <v-avatar>
                    <img src="http://120.79.145.97/image/1-2.png">
                  </v-avatar>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>{{ worker.name }}</v-list-tile-title>
                  <v-list-tile-sub-title>{{subtitle}}</v-list-tile-sub-title>
                </v-list-tile-content>
                <v-list-tile-action>
                  <v-btn icon @click.native="editDialog = true">
                    <v-icon>edit</v-icon>
                  </v-btn>
                </v-list-tile-action>
              </v-list-tile>
            </v-list>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-tabs v-model="tabIndex">
              <v-tab key=0>我的足迹</v-tab>
              <v-tab-item key=0>
              </v-tab-item>
              <v-tab key=1>我的积分</v-tab>
              <v-tab key=2>我的任务</v-tab>
              <v-tab-item key=1>
              </v-tab-item>
            </v-tabs>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
    <v-dialog v-model="editDialog" max-width="500" fullscreen scrollable hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar card dark color="primary">
          <v-btn icon dark @click.native="editDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>修改个人信息</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark flat @click.native="submitItem">保存</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-form :model="worker">
            <v-container fluid>
              <v-layout row>
                <v-flex xs5>
                  <croppa v-model="croppa" placeholder="点击此处上传头像" :quality="1" :zoom-speed="10" prevent-white-space initial-image="/api/avatar"></croppa>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.name" label="用户名" prepend-icon="account_box" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.key" label="密码" prepend-icon="lock" type="password" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-radio-group v-model.number="worker.sex" row prepend-icon="supervisor_account">
                    <v-radio label="男性" v-bind:value=0></v-radio>
                    <v-radio label="女性" v-bind:value=1></v-radio>
                  </v-radio-group>
                </v-flex>
              </v-layout>
              <v-layout row justify-space-between>
                <v-flex xs5>
                  <v-menu ref="menu" :close-on-content-click="false" v-model="dateMenu" :nudge-right="40" lazy
                          transition="scale-transition" offset-y full-width min-width="290px">
                    <v-text-field slot="activator" v-model="worker.birth" label="生日" prepend-icon="event" readonly required></v-text-field>
                    <v-date-picker ref="picker" v-model="worker.birth" :max="new Date().toISOString().substr(0, 10)"
                                   min="1950-01-01" @change="save(worker.birth)"></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.email" label="邮箱" prepend-icon="email" type="email" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.signature" prepend-icon="description" label="个性签名"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "WorkerCenter",
    computed: {
      subtitle() {
        return "经验值："+this.worker.exp+" 积分："+this.worker.credit;
      }
    },
    data() {
      return {
        worker: {},
        tabIndex: 0,
        editDialog: false,
        dateMenu: false,
        croppa: {},
      }
    },
    methods: {
      save(date) {
        this.$refs.menu.save(date)
      },
      submitItem() {
        delete this.worker.id;
        delete this.worker.exp;
        delete this.worker.credit;
        delete this.worker.state;
        if (!this.croppa.hasImage()) {
          bus.$emit('show-info', "请选择头像");
          return;
        }
        this.$http.put('/worker/' + sessionStorage.getItem('id'), this.worker)
          .then((response) => {
            this.editDialog = false;
            this.croppa.generateBlob((blob) => {
              let fd = new FormData();
              fd.append('file', blob);
              fd.append('name', this.worker.name);
              fd.append('type', 'worker');
              this.$http.post('/avatar', fd)
                .then((response) => {
                })
            })
          })
          .catch((error) => {
          })
      },
    },
    created() {
      this.$http.get('/worker/' + sessionStorage.getItem('id'))
        .then((response) => {
          this.worker = response.data;
        })
    }
  }
</script>

<style scoped>

</style>



