<template>
  <v-container>
    <v-layout row justify-space-between>
      <v-flex xs6>
        <v-card class="card">
          <v-card-title>
            <span class="headline">积分排行榜</span>
            <v-spacer></v-spacer>
            <span>我的排名：{{rankData.exp}}</span>
          </v-card-title>
          <v-card-text>
            <v-list>
              <v-list-tile v-for="(worker, index) in expList" :key="index" avatar @click="">
                <v-list-tile-avatar>
                  <v-icon :color="index < 3 ? 'red darken-4' : 'grey darken-3'">{{ 'filter_'+ (index+1)}}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>{{ worker.name }}</v-list-tile-title>
                  <v-list-tile-sub-title>{{ worker.signature }}</v-list-tile-sub-title>
                </v-list-tile-content>
                <v-list-tile-action>
                  {{worker.exp}}
                </v-list-tile-action>
              </v-list-tile>
            </v-list>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs6>
        <v-card class="card">
          <v-card-title>
            <span class="headline">经验值排行榜</span>
            <v-spacer></v-spacer>
            <span>我的排名：{{rankData.credit}}</span>
          </v-card-title>
          <v-card-text>
            <v-list>
              <v-list-tile v-for="(worker, index) in creditList" :key="index" avatar @click="">
                <v-list-tile-avatar>
                  <v-icon :color="index < 3 ? 'red darken-4' : 'grey darken-3'">{{ 'filter_'+ (index+1)}}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>{{ worker.name }}</v-list-tile-title>
                  <v-list-tile-sub-title>{{ worker.signature }}</v-list-tile-sub-title>
                </v-list-tile-content>
                <v-list-tile-action>
                  {{worker.credit}}
                </v-list-tile-action>
              </v-list-tile>
            </v-list>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    name: "WorkerRank",
    data() {
        return {
          expList: [],
          creditList: [],
          rankData: {},
        }
    },
    created() {
      this.$http.get('/worker/data/rank?sorted=exp')
        .then((response) => {
          this.expList = response.data;
        });
      this.$http.get('/worker/data/rank?sorted=credit')
        .then((response) => {
          this.creditList = response.data;
        });
      this.$http.get('/worker/' + sessionStorage.getItem('id') + '/rankData')
        .then((response) => {
          this.rankData = response.data;
        })
    }
  }
</script>

<style scoped>
.card{
  margin: 0 15px;
}
</style>



