<template>
  <v-layout row>
    <v-flex xs12>
      <v-card>
        <v-card-title>
          <span class="headline">{{task.name}}</span>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" to="/worker/index">返回</v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <span class="subheading">{{task.description}}</span>
        </v-card-text>
        <v-container fluid>
          <v-layout row justify-space-between>
            <v-flex xs8>
              <v-app>
                <v-toolbar color="indigo darken-3" height="35">
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="getPrePic" :disabled="preButton">上一张</v-btn>
                  </v-toolbar-items>
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="getNextPic" :disabled="nextButton">下一张</v-btn>
                  </v-toolbar-items>
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="">清空</v-btn>
                  </v-toolbar-items>
                </v-toolbar>
                <v-content>
                  <div id="imgStage" style="position:relative; overflow:auto"></div>
                </v-content>
              </v-app>
            </v-flex>
            <v-flex xs3>
              <v-form>
                <v-layout row v-for="label in task.labels" :key="label.title">
                  <v-text-field v-model="answer[label.title]" :label="label.title" v-if="label.choices.length === 0" required></v-text-field>
                  <v-select v-model="answer[label.title]" :items="label.choices" :label="label.title" required v-else></v-select>
                </v-layout>
                <v-layout row v-if="task.hasWholeLabel">
                  <v-text-field v-model="description" label="整体描述" required></v-text-field>
                </v-layout>
              </v-form>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  import Kinetic from 'kinetic'
  export default {
    name: "SingleMark",
    props: ['task'],
    data() {
      return {
        picOrderArray: [],
        preButton: false,
        nextButton: false,
        picOrder: 0,
        stage: {},
        layer: {},
        imageObj: new Image(),
        square: null,
        answer: {},
        description: '',
      }
    },
    methods: {
      picPath() {
        if (this.task !== null && this.picOrderArray !== null)
          return 'http://120.79.145.97/image/' + this.task.id + '/' + this.picOrderArray[this.picOrder] + '.png'
      },
      submit() {
        let square = this.stage.get("#0")[0];
        return {
          x: square.getWidth() < 0 ? square.getX() + square.getWidth() : square.getX(),
          y: square.getHeight() < 0 ? square.getY() + square.getHeight() : square.getY(),
          h: Math.abs(square.getHeight()),
          w: Math.abs(square.getWidth()),
          description: this.description,
          labelData: this.answer
        };
      },
      update() {
        this.preButton = (this.picOrder === 0);
        this.nextButton = (this.picOrder === this.picOrderArray.length - 1);
        this.imageObj.src = this.picPath();
        this.stage.setWidth(this.imageObj.width);
        this.stage.setHeight(this.imageObj.height);
        this.answer = {};
        this.description = '';
        this.$http.get('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder])
          .then((response) => {
            if (response.data.length > 0) {
              let data = response.data[0];
              this.square = new Kinetic.Rect({
                id: 0,
                x: data.x,
                y: data.y,
                height: data.h,
                width: data.w,
                stroke: 'red',
                strokeWidth: 3
              });
              this.answer = data.labelData;
              this.description = data.description;
              this.layer.draw();
            }
          })
      },
      getPrePic() {
        this.$http.post('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder], new Array(this.submit()))
          .then((response) => {
            this.picOrder --;
            this.update();
          })
          .catch((error) => {
          });
      },
      getNextPic() {
        this.$http.post('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder], new Array(this.submit()))
          .then((response) => {
            this.picOrder ++;
            this.update();
          })
          .catch((error) => {
          });
      },
      clickEvent(event) {
        if (this.square === null) {
          this.square = new Kinetic.Rect({
            id: 0,
            x: event.evt.offsetX,
            y: event.evt.offsetY,
            stroke: 'red',
            strokeWidth: 3
          });
          this.layer.add(this.square);
        } else {
          this.square.setHeight(event.evt.offsetY - this.square.getY());
          this.square.setWidth(event.evt.offsetX - this.square.getX());
          this.layer.draw();
          this.square.remove();
          this.square = null;
        }
      },
      moveEvent(event) {
        if (this.square !== null) {
          this.square.setHeight(event.evt.offsetY - this.square.getY());
          this.square.setWidth(event.evt.offsetX - this.square.getX());
          this.layer.draw();
        }
      }
    },
    created() {
      this.$http.get('/task/' + this.$route.params.id + '/picOrder')
        .then((response) => {
          this.picOrderArray = response.data;

          this.stage = new Kinetic.Stage({
            container: "imgStage", // div的id。同时也可以对这个div进行样式的添加。
            width: 1400,
            height: 500,
          });
          this.layer = new Kinetic.Layer({
            id: "square"
          });
          let layer = new Kinetic.Layer({
            id: "pic"
          });
          let yoda = new Kinetic.Image({
            x: 0,
            y: 0,
            image: this.imageObj,
          });
          this.stage.on("click", this.clickEvent);
          this.stage.on("mousemove", this.moveEvent);
          layer.add(yoda);
          this.stage.add(layer);
          this.stage.add(this.layer);
          this.stage.draw();

          this.update();
        });
    },
  }
</script>

<style scoped>

</style>



