<template>
  <v-layout row>
    <v-flex xs10 offset-xs1>
      <v-card>
        <v-card-title>
          <span class="headline">管理员列表</span>
          <v-spacer></v-spacer>
          <v-btn @click="openDialog(null)">
            <v-icon>add</v-icon>
            <span>增加</span>
          </v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :items="desserts" v-model="selected" item-key="id" hide-actions>
          <template slot="items" slot-scope="props">
            <tr>
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.name }}</td>
              <td>{{ props.item.actualName }}</td>
              <td>{{ getSex(props.item.sex)}}</td>
              <td>{{ getAdminType(props.item.auth)}}</td>
              <td class="layout px-0">
                <v-layout row justify-space-around>
                  <v-flex xs-1>
                    <v-btn icon class="mx-0" @click="openDialog(props.item)">
                      <v-icon color="teal">edit</v-icon>
                    </v-btn>
                  </v-flex>
                  <v-flex xs-1>
                    <v-btn icon class="mx-0" @click="openDelDialog(props.item)">
                      <v-icon color="blue-grey">delete</v-icon>
                    </v-btn>
                  </v-flex>
                </v-layout>
              </td>
            </tr>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">
              没有找到管理员数据 :(
            </v-alert>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
    <v-dialog v-model="editDialog" max-width="500" lazy hide-overlay persistent>
      <v-card>
        <v-card-title class="headline">{{dialogTitle}}</v-card-title>
        <v-card-text>
          <v-form :model="admin">
            <v-container fluid>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="admin.name" label="用户名" prepend-icon="account_box" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="admin.actualName" label="真实姓名" prepend-icon="perm_identity" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="admin.key" label="密码" prepend-icon="lock" type="password" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs10>
                  <v-select :items="authItems" v-model.number="admin.auth" item-value="value" label="权限" prepend-icon="vpn_key"></v-select>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-radio-group v-model.number="admin.sex" row prepend-icon="supervisor_account">
                    <v-radio label="男性" v-bind:value=0></v-radio>
                    <v-radio label="女性" v-bind:value=1></v-radio>
                  </v-radio-group>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click="submitItem()">完成</v-btn>
          <v-btn color="green darken-1" flat @click.native="editDialog = false">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="deleteDialog" max-width="500px" hide-overlay persistent>
      <v-card>
        <v-card-title>
          <span>确认删除管理员：{{admin.name}} ？</span>
          <v-spacer></v-spacer>
        </v-card-title>
        <v-card-actions>
          <v-btn color="red" flat @click="deleteItem">删除</v-btn>
          <v-btn color="primary" flat @click.stop="deleteDialog=false">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "AdminTable",
    computed: {
    },
    data() {
      return {
        drawer: false,
        editDialog: false,
        dialogTitle: '修改管理员信息',
        deleteDialog: false,
        color: 'indigo',
        selected: [],
        authItems: [
          {text: '发起者管理员', value: 1},
          {text: '工人管理员', value: 2}
        ],
        listItem: [
          {icon: 'people', text: '管理员设置', path: '/superAdmin/index'},
          {icon: 'poll', text: '用户数据统计', path: ''},
          {icon: 'timeline', text: '任务数据统计', path: ''},
          //{icon: '', text: '', path: ''},
        ],
        headers: [
          {text: '编号', sortable: true, value: 'id'},
          {text: '用户名', sortable: false, value: 'name'},
          {text: '真实姓名', sortable: false, value: 'actualName'},
          {text: '性别', sortable: false, value: 'sex'},
          {text: '权限', sortable: false, value: 'auth'},
          {text: '操作', sortable: false, value: 'action'}
          //{text: '', sortable: '', value: ''},
        ],
        desserts: [],
        adminIndex: 0,
        adminId: 0,
        admin: {
          name: '',
          actualName: '',
          sex: 0,
          auth: 1,
          key: ''
        }
      }
    },
    methods: {
      getSex: function(sex) {
        return sex === 0 ? '男' : '女';
      },
      getAdminType: function(auth) {
        return auth === 1 ? '发起者管理员' : '工人管理员';
      },
      openDialog(item) {
        if (item == null) {
          this.admin = {name: '', actualName: '', sex: 0, auth: 1, key: ''};
          this.dialogTitle = '新增管理员信息';
          this.adminId = -1;
        } else {
          this.adminId = item.id;
          this.adminIndex = this.desserts.indexOf(item);
          this.$http.get('/admin/'+this.adminId)
            .then((response) => {
              this.admin = response.data;
            });
          this.dialogTitle = '修改管理员信息';
        }
        this.editDialog = true;
      },
      openDelDialog(item) {
        this.deleteDialog = true;
        this.adminId = item.id;
        this.admin.name = item.name;
      },
      submitItem() {
        this.editDialog = false;
        if (this.adminId < 0) {
          this.$http.post('/admin', this.admin)
            .then((response) => {
              bus.$emit('show-info', "成功增加管理员" + this.admin.name);
            });
          this.$http.get('/admin')
            .then((response) => {
              this.desserts = response.data;
            })
        } else {
          this.$http.put('/admin/'+this.adminId+'/setAuth', {auth: this.admin.auth})
            .then((response) => {
            })
            .catch((error) => {
              bus.$emit('show-info', this.admin.name + "管理员权限修改失败");
            });
          const auth = this.admin.auth;
          delete this.admin.auth;
          this.$http.put('/admin/'+this.adminId, this.admin)
            .then((response) => {
              this.admin.auth = auth;
              const newItem = this.admin;
              newItem.id = this.adminId;
              this.desserts.splice(this.adminIndex, 1, newItem);
              console.log(this);
              bus.$emit('show-info', this.admin.name + "管理员修改成功");
            })
        }
      },
      deleteItem() {
        this.deleteDialog = false;
        this.$http.delete('/admin/'+this.adminId)
          .then((response) => {
            this.desserts.splice(this.adminIndex, 1);
            bus.$emit('show-info', this.admin.name + "管理员已被删除");
          })
      }
    },
    created() {
      this.$http.get('/admin')
        .then((response) => {
          this.desserts = response.data;
        })
    },
  }
</script>

<style scoped>

</style>



