<template>
  <v-app id="index" light>
    <v-toolbar id="toolbar" color="purple darken-2" dark app>
    <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
      <span class="hidden-sm-and-down" >{{team}}</span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
      <v-toolbar-items>
      <v-menu open-on-hover bottom offset-y>
        <v-btn slot="activator" color="transparent" depressed class="head_buttons">
          用户登录
          <v-icon dark>arrow_drop_down</v-icon>
        </v-btn>
        <v-list>
          <v-list-tile to="/login/requester">
            <v-list-tile-title>发布者登录</v-list-tile-title>
          </v-list-tile>
          <v-list-tile to="/login/worker">
            <v-list-tile-title>工人登录</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-btn color="transparent" depressed class="head_buttons" to="register">
        用户注册
      </v-btn>
      <v-btn color="transparent" depressed class="head_buttons" to="/login/admin">
        管理员登录
      </v-btn>
      </v-toolbar-items>
    </v-toolbar>

      <v-parallax src="/static/purple_mountain.jpg" height="400">
        <v-layout
          column
          align-center
          justify-center
          class="white--text"
        >
          <h1 id="title" class="white--text mb-2 display-3 text-xs-center">{{title}}</h1>
          <div class="subheading mb-3 text-xs-center">{{sub_title}}</div>
          <v-btn class="purple darken-1 mt-5" dark large>
            Get Started
          </v-btn>
        </v-layout>
      </v-parallax>

    <v-content id="content">
      <section>
        <v-layout column wrap class="my-5" align-center>
          <v-flex xs12 sm4 class="my-3">
            <div class="text-xs-center">
              <h2 class="headline">{{intro_title}}</h2>
              <span class="subheading">{{intro_sub_title}}</span>
            </div>
          </v-flex>
          <v-flex xs12>
            <v-container grid-list-xl>
              <v-layout row wrap align-center>
                <v-flex xs12 md6 text-xs-center>
                  <v-card class="elevation-0 transparent">
                    <v-card-text class="text-xs-center">
                      <v-icon x-large class="purple--text text--darken-2">pan_tool</v-icon>
                    </v-card-text>
                    <v-card-title primary-title class="layout justify-center">
                      <div class="headline text-xs-center" id="intro_publisher">发布者</div>
                    </v-card-title>
                    <v-card-text class="introduction" id="intro_pub_text">{{intro_publisher}}</v-card-text>
                    <v-btn class="purple darken-2 mt-4" dark large>
                      发布者指南
                    </v-btn>
                  </v-card>
                </v-flex>
                <v-flex xs12 md6 text-xs-center>
                  <v-card class="elevation-0 transparent">
                    <v-card-text class="text-xs-center">
                      <v-icon x-large color="indigo darken-3">build</v-icon>
                    </v-card-text>
                    <v-card-title primary-title class="layout justify-center">
                      <div class="headline text-xs-center" id="intro_worker">工人</div>
                    </v-card-title>
                    <v-card-text class="introduction" id="intro_worker_text">{{intro_worker}}</v-card-text>
                    <v-btn class="indigo darken-3 mt-4" dark large>
                      工人指南
                    </v-btn>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>
      </section>

      <section>
        <v-parallax src="/static/light_train.jpg" height="400">
          <v-layout column align-center justify-center>
            <v-flex xs12 sm10 md10 text-xs-center>
              <div class="headline white--text mb-3 text-xs-center">{{intro_all}}</div>
              <em>新人朋友先注册噢</em>
              <br/>
              <v-btn icon dark>
                <v-icon x-large>touch_app</v-icon>
              </v-btn>
            </v-flex>
          </v-layout>
        </v-parallax>
      </section>

      <section>
        <v-container grid-list-xl>
          <v-layout row wrap justify-center class="my-5">
            <v-flex xs12 sm4>
              <v-card class="elevation-0 transparent">
                <v-card-title primary-title class="layout justify-center">
                  <div class="headline">关于我们</div>
                </v-card-title>
                <v-card-text>{{about_us}}</v-card-text>
              </v-card>
            </v-flex>
            <v-flex xs12 sm4 offset-sm1>
              <v-card class="elevation-0 transparent">
                <v-card-title primary-title class="layout justify-center">
                  <div class="headline">联系我们</div>
                </v-card-title>
                <v-card-text>
                  如您想吐槽，请不要联系我们。如果您想撩汉，欢迎联系我们。
                </v-card-text>
                <v-list class="transparent">
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon class="indigo--text text--darken-3">phone</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>123-456-7890</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon class="indigo--text text--darken-3">place</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>Nanjing University Xianlin Campus</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-list-tile>
                    <v-list-tile-action>
                      <v-icon class="indigo--text text--darken-3">email</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>AlphaCat@163.com</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                </v-list>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </section>
      <v-footer class="indigo darken-3" id="footer">
        <v-layout row wrap align-center>
          <v-flex xs12 text-xs-center>
            <div class="white--text ml-3">
              Made with
              <v-icon class="red--text">favorite</v-icon>
              by AlphaCat Team
            </div>
          </v-flex>
        </v-layout>
      </v-footer>
    </v-content>
  </v-app>
</template>

<script>

  export default {
    name: "Index",
    data() {
      return {
        team: 'AlphaCat',
        title: '众包标注平台',
        sub_title: 'Crowdsourcing Tags',
        intro_title: '为人工智能和大数据分析服务的海量数据征集平台',
        intro_sub_title: '工人&发布者',
        intro_worker: '这里为您提供一份工作，利用碎片化时间，动动鼠标，随时随地都可以进行，并且获取不错的报酬。这里更是一个可以不断成长的平台，您可以通过参与大量任务，提升自己的标注准确率，从而提升经验值。当您晋升为高级工人，当然您的报酬也会随之提升。',
        intro_publisher: '您还在到处寻找为数据标注的工人吗？这里聚集了一群经验丰富的工人，他们经过大量标注任务的训练。在这里，您可以自定义任务的发布，诸如单框标注、多框标注以及不规则标等。如果您加入我们的会员，您还可以享受更多服务，全方位提高您采集到的数据质量。',
        intro_all: '当然，还有更多的功能等待您去发掘！赶快加入我们吧~',
        about_us: '我们是南京九乡河技术学院的一支团结友爱积极向上的软件开发小分队，我们的艺名是AlphaCat。目前我们有四只程序猿，其中还有一只外院系乱入的，所以我们从人力资源和硬件资源方面看，都处于落后水平。我们真诚并且热烈欢迎来自社会各界的捐赠，感谢你们的支持！'
      }
    }
  }
</script>

<style scoped>
  #title{
    text-align: center;
    font-weight: bolder;
    color: floralwhite;
    text-shadow: 2px 2px 2px grey;
  }
  .headline{
    color: dimgray;
    text-shadow: 1px 1px 1px grey;
  }
  #intro_publisher{
    color: #7B1FA2;
    text-shadow: 1px 1px 1px grey;
  }
  #intro_worker{
    color: #283593;
    text-shadow: 1px 1px 1px grey;
  }
  .introduction{
    text-align: center;
    font-family: "华文黑体";
    color: dimgray;
  }
  #toolbar{
    opacity: 0.7;
  }
  .head_buttons{
      font-size: larger;
      color: snow;
      font-weight: bolder;
  }
  #intro_pub_text{
    font-size: larger;
  }

  #intro_worker_text{
    font-size: larger;
  }

  #footer{
    opacity: 0.8;
  }
</style>



