import VueRouter from 'vue-router'
import Index from './components/Index'
import Register from './components/Register'
import AdminPage from './components/admin/AdminPage'
import AdminInfo from './components/admin/AdminInfo'
import RequesterCheck from './components/admin/RequesterCheck'
import NoticePage from './components/admin/NoticePage'
import WorkerPage from './components/worker/WorkerPage'
import WorkerIndex from './components/worker/WorkerIndex'
import WorkerTask from './components/worker/WorkerTask'
import WorkerMark from './components/worker/WorkerMark'
import WorkerRank from './components/worker/WorkerRank'
import WorkerCenter from './components/worker/WorkerCenter'
import IndexPage from './components/admin/IndexPage'
import WorkerLogin from './components/WorkerLogin'
import RequesterLogin from './components/RequesterLogin'
import AdminLogin from './components/AdminLogin'
const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      meta: {title: '首页 | AlphaCat众包数据标注系统'},
      component: Index
    },
    {
      path: '/login/worker',
      name: 'WorkerLogin',
      meta: {title: '工人登录 | AlphaCat众包数据标注系统'},
      component: WorkerLogin
    },
    {
      path: '/login/requester',
      name: 'PublisherLogin',
      meta: {title: '发布者登录 | AlphaCat众包数据标注系统'},
      component: RequesterLogin,
    },
    {
      path: '/login/admin',
      name: 'AdminLogin',
      meta: {title: '管理员登录 | AlphaCat后台管理系统'},
      component: AdminLogin
    },
    {
      path: '/register',
      name: 'Register',
      meta: {title: '注册 | AlphaCat众包数据标注系统'},
      component: Register
    },
    {
      path: '/admin',
      name: 'AdminPage',
      component: AdminPage,
      children: [
        {
          path: 'index',
          component: IndexPage,
          meta: {title: '首页 | 后台管理系统'},
        },
        {
          path: 'info',
          component: AdminInfo,
          meta: {title: '个人信息 | 后台管理系统'},
        },
        {
          path: 'check',
          component: RequesterCheck,
          meta: {title: '发布者审核 | 后台管理系统'},
        },
        {
          path: 'notice',
          component: NoticePage,
          meta: {title: '通知管理 | 后台管理系统'},
        }
      ],
    },
    {
      path: '/worker',
      name: 'WorkerPage',
      component: WorkerPage,
      children: [
        {
          path: 'index',
          component: WorkerIndex,
          meta: {title: '首页 | AlphaCat众包平台'},
        },
        {
          path: 'task',
          component: WorkerTask,
          meta: {title: '任务 | AlphaCat众包平台'},
        },
        {
          path: 'mark/:id',
          component: WorkerMark,
          meta: {title: '标注 | AlphaCat众包平台'},
        },
        {
          path: 'rank',
          component: WorkerRank,
          meta: {title: '排名 | AlphaCat众包平台'},
        },
        {
          path: 'center',
          component: WorkerCenter,
          meta: {title: '个人中心 | AlphaCat众包平台'},
        }
      ]
    },
  ]
});
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
});

export default router



