<template>
  <v-container>
    <v-layout row>
      <v-flex xs9>
        <v-card class="taskPanel">
          <v-card-title>
            <span class="headline">任务一览</span>
            <v-spacer></v-spacer>
            <v-btn-toggle v-model="showType" mandatory>
              <v-btn flat>
                <v-icon>view_list</v-icon>
              </v-btn>
              <v-btn flat>
                <v-icon>view_module</v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-card-title>
          <v-tabs light v-model="active" slider-color="indigo">
            <v-tab key=0 ripple>
              我的任务
            </v-tab>
            <v-tab-item key=0>
              <TaskPanel :show-type="showType" type="available"></TaskPanel>
            </v-tab-item>
            <v-tab key=1 ripple>
              已参与任务
            </v-tab>
            <v-tab-item key=1>
              <TaskPanel :show-type="showType" type="doing"></TaskPanel>
            </v-tab-item>
            <v-spacer></v-spacer>
            <v-text-field v-model="searchName" prepend-icon="search" label="搜索任务名称" single-line small></v-text-field>
          </v-tabs>
        </v-card>
      </v-flex>
      <v-flex xs3>
        <InfoPanel></InfoPanel>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import InfoPanel from "./InfoPanel";
  import TaskPanel from "./TaskPanel";
  export default {
    name: "WorkerTask",
    components: {TaskPanel, InfoPanel},
    computed: {
    },
    data() {
      return {
        showType: 0,
        active: 0,
        searchName: '',
        headers: [
          {text: '编号', sortable: true, value: 'id'},
          {text: '任务名', sortable: false, value: 'name'},
          {text: '结束时间', sortable: true, value: 'endTime'},
          {text: '单张奖励', sortable: false, value: 'creditPerPic'},
          {text: '完成奖励', sortable: false, value: 'creditFinished'},
        ],
        items: []
      }
    },
    methods: {
      getMethodName(method) {
        switch (method) {
          case 1: return '单框标注';
          case 2: return '多框标注';
          case 3: return '不规则标注';
        }
      },
      taskFilter(val, search) {
        return val.indexOf(search) !== -1;
      },
      customFilter(items, search, filter) {
        let results = [];
        for (let item in items) {
          if (filter(this.items[item].name, search)) results.push(this.items[item]);
        }
        return results;
      },
      updateTasks() {
        this.$http.get('/task?type=available&workerId=' + sessionStorage.getItem('id'))
          .then((response) => {
            this.items = response.data;
          })
      }
    },
    created() {
      this.updateTasks();
    }
  }
</script>

<style scoped>
.taskPanel{
  margin-right: 15px;
}
</style>



