<template>
  <v-layout row>
    <v-flex xs10 offset-xs1>
      <v-card>
        <v-card-title>
          <span class="headline">已发布通知</span>
          <v-spacer></v-spacer>
          <v-btn @click="openDialog">
            <v-icon>add</v-icon>
            <span>增加</span>
          </v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :items="items" item-key="id" hide-actions>
          <template slot="items" slot-scope="props">
            <tr @click="props.expanded = !props.expanded">
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.title }}</td>
              <td>{{ props.item.startDate }}</td>
              <td>{{ props.item.endDate }}</td>
              <td>{{ getUserType(props.item.type)}}</td>
              <td class="layout px-0">
                <v-layout row justify-space-around>
                  <v-flex xs-1>
                    <v-btn icon class="mx-0" @click="openDelDialog(props.item)">
                      <v-icon color="blue-grey">delete</v-icon>
                    </v-btn>
                  </v-flex>
                </v-layout>
              </td>
            </tr>
          </template>
          <template slot="expand" slot-scope="props">
            <v-card flat>
              <v-card-text>{{props.item.content}}</v-card-text>
            </v-card>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">
              没有找到通知数据 :(
            </v-alert>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
    <v-dialog v-model="editDialog" max-width="650" lazy hide-overlay persistent>
      <v-card>
        <v-card-title class="headline">发布通知</v-card-title>
        <v-card-text>
          <v-form :model="notice">
            <v-container fluid>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="notice.title" label="标题" prepend-icon="send" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="notice.content" label="内容" prepend-icon="notes" multi-line required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-switch v-model="forever" :label="forever ? '永久' : '限时'" prepend-icon="open_in_new"></v-switch>
                </v-flex>
              </v-layout>
              <v-layout row justify-space-between v-if="!forever">
                <v-flex xs10>
                  <v-menu ref="menu" :close-on-content-click="false" v-model="dateMenu" :nudge-right="40" lazy
                          transition="scale-transition" offset-y full-width min-width="290px">
                    <v-text-field slot="activator" v-model="notice.endDate" label="结束日期" prepend-icon="event" readonly required></v-text-field>
                    <v-date-picker ref="picker" v-model="notice.endDate" :max="new Date().toISOString().substr(0, 10)"
                                   min="1950-01-01" @change="save(notice.endDate)"></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-radio-group v-model.number="notice.type" label="通知对象" prepend-icon="how_to_reg">
                    <v-radio label="发布者" v-bind:value=0></v-radio>
                    <v-radio label="工人" v-bind:value=1></v-radio>
                  </v-radio-group>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click="submitItem">发布</v-btn>
          <v-btn color="green darken-1" flat @click.native="editDialog = false">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="deleteDialog" max-width="500px" hide-overlay persistent>
      <v-card>
        <v-card-title>
          <span>确认删除此通知：{{notice.title}} ？</span>
          <v-spacer></v-spacer>
        </v-card-title>
        <v-card-actions>
          <v-btn color="red" flat @click="deleteItem">删除</v-btn>
          <v-btn color="primary" flat @click.stop="deleteDialog=false">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "NoticePage",
    data() {
      return {
        editDialog: false,
        deleteDialog: false,
        dateMenu: false,
        forever: true,
        headers: [
          {text: '编号', sortable: true, value: 'id'},
          {text: '标题', sortable: false, value: 'title'},
          {text: '发布日期', sortable: false, value: 'startDate'},
          {text: '结束日期', sortable: false, value: 'endDate'},
          {text: '通知对象', sortable: false, value: 'type'},
        ],
        items: [],
        notice: {
          title: '',
          content: '',
          endDate: '',
          type: 0
        },
      }
    },
    methods: {
      getUserType: function (type) {
        return type === 0 ? '发起者' : '工人';
      },
      openDialog() {
        this.notice = {title: '', content: '', type: 0};
        this.editDialog = true;
      },
      openDelDialog(item) {
        this.deleteDialog = true;
        this.notice = item;
      },
      save(date) {
        this.$refs.menu.save(date)
      },
      update() {
        this.$http.get('/notice')
          .then((response) => {
            this.items = response.data;
          })
      },
      deleteItem() {
        this.deleteDialog = false;
        this.$http.delete('/notice/'+this.notice.id)
          .then((response) => {
            bus.$emit('show-info', "通知已成功删除");
          })
      },
      submitItem() {
        this.editDialog = false;
        if (this.forever) this.notice.endDate = '0';
        this.$http.post('/notice', this.notice)
          .then((response) => {
            this.update();
            bus.$emit('show-info', "通知已成功发布");
          })
      }
    },
    created() {
      this.update();
    }
  }
</script>

<style scoped>

</style>



