<template>
  <v-content>
    <v-layout row>
      <v-flex xs12>
        <v-card>
          <v-card-media class="white--text" height="130px" src="/static/triangle3.jpg">
            <v-container fill-height fluid>
              <v-layout row wrap>
                <v-flex xs12 class="white--text">
                  <p class="display-1 text-xs-center">{{workerName}}</p>
                  <p class="subheading text-xs-right">{{signature}}</p>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-media>
          <v-list>
            <v-list-tile>
              <v-list-tile-action>
                <v-icon color="indigo">storage</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>{{level}}</v-list-tile-title>
                <v-list-tile-sub-title>等级</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile v-for="item in listItem" :key="item.value">
              <v-list-tile-action>
                <v-icon color="indigo">{{item.icon}}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>{{item.data}}</v-list-tile-title>
                <v-list-tile-sub-title>{{item.text}}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
          <v-card-actions>
            <v-flex xs6>
              <v-btn block color="orange lighten-2" :disabled="hasSign" @click="sign">{{signBtnText}}</v-btn>
            </v-flex>
            <v-flex xs6>
              <p></p>
              <p class="text-xs-center">已签到{{signDays}}天</p>
            </v-flex>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-content>
</template>

<script>
  export default {
    name: "InfoPanel",
    computed: {
      //积分计算方法
      level() {
        let result = 1;
        let exp = this.getItem('exp');
        if (exp <= 1000) {
          result += Math.round(exp / 100);
        } else {
          result = 10;
          result += Math.round((exp -1000) / 250);
        }
        return result;
      },
      signBtnText() {
        return this.hasSign ? "已签到" : "点击签到";
      }
    },
    data() {
      return {
        workerName: sessionStorage.getItem("name"),
        listItem: [
          {icon: 'attach_money', text: '积分', data: 0, value: 'credit'},
          {icon: 'pages', text: '经验值', data: 0, value: 'exp'},
          {icon: 'email', text: '邮箱', data: '', value: 'email'}
        ],
        signature: '',
        hasSign: false,
        signDays: 0,
      }
    },
    methods: {
      getItem(value) {
        return this.listItem.find((item) => {return item.value === value}).data;
      },
      getWorkerInfo() {
        this.$http.get('/worker/' + sessionStorage.getItem('id'))
          .then((response) => {
            this.listItem[0].data = response.data.credit;
            this.listItem[1].data = response.data.exp;
            this.listItem[2].data = response.data.email;
            this.signature = response.data.signature;
          })
      },
      getWorkerSignInfo() {
        this.$http.get('/worker/'+ sessionStorage.getItem('id') + '/signup')
          .then((response) => {
            this.hasSign = response.data.hasSign;
            this.signDays = response.data.daysNum;
          })
      },
      sign() {
        this.$http.post('/worker/'+ sessionStorage.getItem('id') + '/signup')
          .then((response) => {
            this.hasSign = true;
            this.signDays += 1;
          })
      }
    },
    created() {
      this.getWorkerInfo();
      this.getWorkerSignInfo();
    }
  }
</script>

<style scoped>

</style>



