<template>
  <v-app>
    <v-toolbar id="toolbar" color="transparent" dark app flat>
      <v-spacer></v-spacer>
      <v-btn color="transparent" depressed class="head_buttons" to="/">
        <v-icon dark left>reply</v-icon>
        返回首页
      </v-btn>
    </v-toolbar>
    <v-jumbotron src="/static/light_train.jpg" dark height="200px" :gradient="gradient">
      <v-container fill-height>
        <v-layout align-center class="text-lg-left">
          <v-flex>
            <h3 class="display-2">{{title}}</h3>
            <span class="subheading">{{sub_title}}</span>
          </v-flex>
        </v-layout>
      </v-container>
    </v-jumbotron>
    <v-content>
      <v-container>
        <v-stepper v-model="step">
          <v-stepper-header>
            <v-stepper-step :complete="step > 1" step="1">选择注册身份</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step :complete="step > 2" step="2">完善个人信息</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3">注册完成</v-stepper-step>
          </v-stepper-header>
          <v-stepper-items>
            <v-stepper-content step="1">
              <v-layout row justify-space-around>
                <v-flex md5>
                  <v-card>
                    <v-card-media src="static/publisher.jpg" height="310px">
                    </v-card-media>
                    <v-card-title>
                      <v-flex xs12>
                        <div class="text-xs-center">
                          <h3 class="headline">注册成为发布者</h3>
                          <div>上传图片、设定条件、查看统计与分析，制定你的专属标注任务！</div>
                        </div>
                      </v-flex>
                    </v-card-title>
                    <v-card-actions>
                      <v-flex xs12>
                        <div class="text-xs-center">
                          <v-btn color="green darken-2" large @click="chooseType('requester')">立即注册</v-btn>
                        </div>
                      </v-flex>
                    </v-card-actions>
                  </v-card>
                </v-flex>
                <v-flex md5>
                  <v-card>
                    <v-card-media src="static/worker.jpg" height="310px">
                    </v-card-media>
                    <v-card-title>
                      <v-flex xs12>
                        <div class="text-xs-center">
                          <h3 class="headline">注册成为工人</h3>
                          <div>参加任务、进行标注、获取奖励、兑换奖品，走向现充之路！</div>
                        </div>
                      </v-flex>
                    </v-card-title>
                    <v-card-actions>
                      <v-flex xs12>
                        <div class="text-xs-center">
                          <v-btn color="blue darken-2" large @click="chooseType('worker')">立即注册</v-btn>
                        </div>
                      </v-flex>
                    </v-card-actions>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-stepper-content>
            <v-stepper-content step="2">
              <v-form v-if="userType == 'requester'" :model="requester">
                <v-container fluid>
                  <v-layout row>
                    <v-flex xs5>
                      <croppa v-model="croppa" placeholder="点击此处上传头像" :quality="1" :zoom-speed="8" prevent-white-space></croppa>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="requester.name" label="用户名" prepend-icon="account_box" required></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="requester.key" label="密码" prepend-icon="lock" type="password" required></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row justify-space-between>
                    <v-flex xs5>
                      <v-menu
                        ref="menu"
                        :close-on-content-click="false"
                        v-model="dateMenu"
                        :nudge-right="40"
                        lazy
                        transition="scale-transition"
                        offset-y
                        full-width
                        min-width="290px"
                      >
                        <v-text-field
                          slot="activator"
                          v-model="requester.birth"
                          label="生日"
                          prepend-icon="event"
                          readonly
                          required
                        ></v-text-field>
                        <v-date-picker
                          ref="picker"
                          v-model="requester.birth"
                          :max="new Date().toISOString().substr(0, 10)"
                          min="1950-01-01"
                          @change="save(requester.birth)"
                        ></v-date-picker>
                      </v-menu>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-radio-group v-model="requester.sex" row prepend-icon="supervisor_account" label="性别">
                        <v-radio label="男性" value="0"></v-radio>
                        <v-radio label="女性" value="1"></v-radio>
                      </v-radio-group>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="requester.email" label="邮箱" prepend-icon="email" type="email" required></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="requester.occupation" prepend-icon="turned_in" label="职务"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="requester.company" prepend-icon="card_travel" label="公司"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs2>
                      <v-btn color="green darken-2" @click="requester_submit" block>注册</v-btn>
                    </v-flex>
                    <v-flex xs2 offset-xs1>
                      <v-btn color="light-green" @click="step = 1" block>上一步</v-btn>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-form>
              <v-form v-else-if="userType == 'worker'" :model="requester">
                <v-container fluid>
                  <v-layout row>
                    <v-flex xs5>
                      <croppa v-model="croppa" placeholder="点击此处上传头像" :quality="1" :zoom-speed="10" prevent-white-space></croppa>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="worker.name" label="用户名" prepend-icon="account_box" required></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="worker.key" label="密码" prepend-icon="lock" type="password" required></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row justify-space-between>
                    <v-flex xs5>
                      <v-menu
                        ref="menu"
                        :close-on-content-click="false"
                        v-model="dateMenu"
                        :nudge-right="40"
                        lazy
                        transition="scale-transition"
                        offset-y
                        full-width
                        min-width="290px"
                      >
                        <v-text-field
                          slot="activator"
                          v-model="worker.birth"
                          label="生日"
                          prepend-icon="event"
                          readonly
                          required
                        ></v-text-field>
                        <v-date-picker
                          ref="picker"
                          v-model="worker.birth"
                          :max="new Date().toISOString().substr(0, 10)"
                          min="1950-01-01"
                          @change="save(worker.birth)"
                        ></v-date-picker>
                      </v-menu>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-radio-group v-model="worker.sex" row label="性别" prepend-icon="supervisor_account">
                        <v-radio label="男性" value="0"></v-radio>
                        <v-radio label="女性" value="1"></v-radio>
                      </v-radio-group>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="worker.email" label="邮箱" prepend-icon="email" type="email" required></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs5>
                      <v-text-field v-model="worker.signature" prepend-icon="description" label="个性签名"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs2>
                      <v-btn color="blue darken-2" @click="worker_submit" block>注册</v-btn>
                    </v-flex>
                    <v-flex xs2 offset-xs1>
                      <v-btn color="light-blue lighten-2" @click="step = 1" block>上一步</v-btn>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-form>
            </v-stepper-content>
            <v-stepper-content step="3">
              <div class="display-1">注册成功！</div>
              <v-btn color="primary" @click.native="step = 1" to="/login">现在登录</v-btn>
              <v-btn to="/index">返回首页</v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "Register",
    data() {
      return {
        title: '众包标注平台——用户注册',
        sub_title: 'Crowdsourcing Tags',
        gradient: 'to top right, rgba(0,0,128,.6), rgba(136,0,128, .6',
        step: 1,
        userType: '',
        dateMenu: false,
        requester: {
          name: '',
          key: '',
          birth: null,
          sex: '0',
          email: '',
          occupation: '',
          company: ''
        },
        worker : {
          name:'',
          key:'',
          birth: null,
          sex: '0',
          email: '',
          signature: ''
        },
        croppa: {},
      }
    },
    watch: {
      dateMenu (val) {
        val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'))
      }
    },
    methods: {
      chooseType(type) {
        this.userType = type;
        this.step = 2;
      },
      save(date) {
        this.$refs.menu.save(date)
      },
      requester_submit() {
        if (!this.croppa.hasImage()) {
          bus.$emit('show-info', "请选择头像");
          return;
        }
        this.$http.post('/requester', this.requester)
          .then((response) => {
            this.step = 3;
            //this.upload(this.requester.name, 'requester');
          })
          .catch((error) => {
            bus.$emit('show-info', "注册失败："+error.response.data.message);
          })
      },
      worker_submit() {
        if (!this.croppa.hasImage()) {
          bus.$emit('show-info', "请选择头像");
          return;
        }
        this.$http.post('/worker', this.worker)
          .then((response)=>{
            this.step = 3;
            //this.upload(this.worker.name, 'worker');
          })
          .catch((error)=>{
            bus.$emit('show-info', "注册失败："+error.response.data.message);
          })
      },
      upload(name, type) {
        this.croppa.generateBlob((blob) => {
          let fd = new FormData();
          fd.append('file', blob);
          fd.append('name', name);
          fd.append('type', type);
          this.$http.post('/avatar', fd)
            .then((response) => {

            })
        })
      }
    }
  }
</script>

<style scoped>

</style>



