<template>
  <v-layout row>
    <v-flex xs10 offset-xs1>
      <v-card>
        <v-card-title>
          <span class="headline">工人列表</span>
          <v-spacer></v-spacer>
              <v-select :items="types" v-model="type" label="筛选" min-width="200" style="width: 200px" single-line></v-select>
        </v-card-title>
        <v-data-table :headers="headers" :items="items" item-key="id" hide-actions>
          <template slot="items" slot-scope="props">
            <tr>
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.name }}</td>
              <td>{{ props.item.birth }}</td>
              <td>{{ getSex(props.item.sex)}}</td>
              <td>{{ props.item.email }}</td>
              <td>{{ props.item.exp }}</td>
              <td>{{ props.item.credit }}</td>
              <td class="layout px-0">
                <v-layout row justify-space-around>
                  <v-flex xs-1>
                    <v-btn icon class="mx-0" @click="openDialog(props.item.id)">
                      <v-icon color="teal">edit</v-icon>
                    </v-btn>
                  </v-flex>
                  <v-flex>
                    <v-btn class="mx-0" @click="lock(props.item)" v-if="props.item.state === 0">
                      <v-icon color="red darken-3">lock</v-icon>封禁
                    </v-btn>
                    <v-btn class="mx-0" @click="unlock(props.item)" v-if="props.item.state === 1">
                      <v-icon color="blue darken-4">reply</v-icon>解封
                    </v-btn>
                  </v-flex>
                </v-layout>
              </td>
            </tr>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">
              没有找到工人的数据 :(
            </v-alert>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
    <v-dialog v-model="editDialog" max-width="500" fullscreen scrollable hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar card dark color="primary">
          <v-btn icon dark @click.native="editDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>修改工人信息</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark flat @click.native="submitItem">保存</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-form :model="worker">
            <v-container fluid>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.name" label="用户名" prepend-icon="account_box" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.key" label="密码" prepend-icon="lock" type="password" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-radio-group v-model.number="worker.sex" row prepend-icon="supervisor_account">
                    <v-radio label="男性" v-bind:value=0></v-radio>
                    <v-radio label="女性" v-bind:value=1></v-radio>
                  </v-radio-group>
                </v-flex>
              </v-layout>
              <v-layout row justify-space-between>
                <v-flex xs5>
                  <v-menu ref="menu" :close-on-content-click="false" v-model="dateMenu" :nudge-right="40" lazy
                          transition="scale-transition" offset-y full-width min-width="290px">
                    <v-text-field slot="activator" v-model="worker.birth" label="生日" prepend-icon="event" readonly required></v-text-field>
                    <v-date-picker ref="picker" v-model="worker.birth" :max="new Date().toISOString().substr(0, 10)"
                                   min="1950-01-01" @change="save(worker.birth)"></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.email" label="邮箱" prepend-icon="email" type="email" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="worker.signature" prepend-icon="description" label="个人签名"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>

                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "WorkerTable",
    data() {
      return {
        editDialog: false,
        dateMenu: false,
        types: [
          {text: '全部', value: 'all'},
          {text: '正常用户', value: 'active'},
          {text: '已封禁用户', value: 'locked'},
        ],
        type: 'all',
        headers: [
          {text: '编号', sortable: true, value: 'id'},
          {text: '用户名', sortable: true, value: 'name'},
          {text: '生日', sortable: false, value: 'birth'},
          {text: '性别', sortable: false, value: 'sex'},
          {text: '邮箱', sortable: false, value: 'email'},
          {text: '经验值', sortable: false, value: 'exp'},
          {text: '积分', sortable: false, value: 'credit'},
          {text: '操作', sortable: false, value: ''},
        ],
        items: [],
        workerId: 0,
        worker: {
          id: 0,
          name: '',
          key: '',
          birth: '',
          sex: '',
          email: '',
          signature: '',
        }
      }
    },
    watch: {
      type() {
        this.updateTable();
      }
    },
    methods: {
      getSex: function(sex) {
        return sex === 0 ? '男' : '女';
      },
      save(date) {
        this.$refs.menu.save(date)
      },
      updateTable() {
        this.$http.get('/worker?type=' + this.type)
          .then((response) => {
            this.items = response.data;
          })
      },
      openDialog(id) {
        this.$http.get('/worker/' + id)
          .then((response) => {
            this.workerId = id;
            this.worker = response.data;
            this.editDialog = true;
          })
          .catch((error) => {
            bus.$emit('show', error.data)
          })
      },
      submitItem() {
        delete this.worker.id;
        delete this.worker.exp;
        delete this.worker.credit;
        delete this.worker.state;
        this.$http.put('/worker/' + this.workerId, this.worker)
          .then((response) => {
            this.updateTable();
            this.editDialog = false;
            bus.$emit('show-info', this.worker.name + "工人信息修改成功");
          })
          .catch((error) => {
            bus.$emit('show-info', error.data)
          })
      },
      unlock(item) {
        this.$http.post('/worker/unlock/' + item.id)
          .then((response) => {
            this.updateTable();
            bus.$emit('show-info', this.worker.name + "工人已被解封");
          })
          .catch((error) => {
            bus.$emit('show-info', error.data)
          })
      },
      lock(item) {
        this.$http.post('/worker/lock/' + item.id)
          .then((response) => {
            this.updateTable();
            bus.$emit('show-info', this.worker.name + "工人已被封禁");
          })
          .catch((error) => {
            bus.$emit('show-info', error.data)
          })
      },
    },
    created() {
      this.updateTable();
    }
  }
</script>

<style scoped>

</style>



