<template>
  <v-layout row>
    <v-flex xs10 offset-xs1>
      <v-card>
        <v-card-title>
          <span class="headline">待审核发布者列表</span>
          <v-spacer></v-spacer>
          <v-btn @click="checkAll">
            <v-icon>done_all</v-icon>
            <span>全部通过</span>
          </v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :items="items" item-key="id" hide-actions>
          <template slot="items" slot-scope="props">
            <tr>
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.name }}</td>
              <td>{{ props.item.birth }}</td>
              <td>{{ getSex(props.item.sex)}}</td>
              <td>{{ props.item.email }}</td>
              <td class="layout px-0">
                <v-layout row justify-space-around>
                  <v-btn icon class="mx-0" @click="check(props.item, true)">
                    <v-icon color="primary">check</v-icon>
                  </v-btn>
                  <v-btn icon class="mx-0" @click="check(props.item, false)">
                    <v-icon color="red">close</v-icon>
                  </v-btn>
                </v-layout>
              </td>
            </tr>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">
              没有需要审核的发起者 :(
            </v-alert>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  import bus from '@/eventBus'
    export default {
      name: "RequesterCheck",
      data() {
        return {
          headers: [
            {text: '编号', sortable: true, value: 'id'},
            {text: '用户名', sortable: true, value: 'name'},
            {text: '生日', sortable: false, value: 'birth'},
            {text: '性别', sortable: false, value: 'sex'},
            {text: '邮箱', sortable: false, value: 'email'},
            {text: '操作', sortable: false, value: ''},
          ],
          items: [],
        }
      },
      methods: {
        getSex: function (sex) {
          return sex === 0 ? '男' : '女';
        },
        updateTable() {
          this.$http.get('/requester?type=unchecked')
            .then((response) => {
              this.items = response.data;
            })
        },
        check(item, flag) {
          let postData = {
            id: item.id,
            isChecked: flag
          };
          this.$http.post('/requester/check', postData)
            .then((response) => {
              this.items.splice(this.items.indexOf(item), 1);
              bus.$emit('show-info', item.name + "已审核" + (flag ? "通过":"不通过"));
            })
            .catch((error) => {
              bus.$emit('show-info', error.body);
            })
        },
        checkAll() {
          let flag = true;
          for (let item in this.items) {
            let postData = {
              id: item.id,
              isChecked: true
            };
            this.$http.post('/requester/check', postData)
              .catch((error) => {
                bus.$emit('show-info', error.data);
                flag = false;
              })
          }
          if (flag) bus.$emit('show-info', "已全部审核完毕");
          this.updateTable()
        }
      },
      created() {
        this.updateTable();
      }
    }
</script>

<style scoped>

</style>



