<template>
  <v-container fluid fill-height>
    <AdminTable v-if="adminType === 'superAdmin'"></AdminTable>
    <RequesterTable v-if="adminType === 'requesterAdmin'"></RequesterTable>
    <WorkerTable v-if="adminType === 'workerAdmin'"></WorkerTable>
  </v-container>
</template>

<script>
    import AdminTable from "./AdminTable";
    import RequesterTable from "./RequesterTable";
    import WorkerTable from "./WorkerTable";
    export default {
      name: "IndexPage",
      components: {WorkerTable, RequesterTable, AdminTable},
      data() {
        return {
          adminType: sessionStorage.getItem("type"),
        }
      }
    }
</script>

<style scoped>

</style>



