<template>
  <v-container fluid>
    <SingleMark v-if="this.task.method === 1" :task="task"></SingleMark>
    <MultiMark v-if="this.task.method === 2" :task="task"></MultiMark>
    <AreaMark v-if="this.task.method === 3" :task="task"></AreaMark>
  </v-container>
</template>

<script>
  import SingleMark from "./SingleMark";
  import MultiMark from "./MultiMark";
  import AreaMark from "./AreaMark";
  export default {
    name: "WorkerMark",
    components: {AreaMark, MultiMark, SingleMark},
    data() {
      return {
        task: {},
      }
    },
    created() {
      this.$http.get('/task/' + this.$route.params.id)
        .then((response) => {
          this.task = response.data;
        });
    },
  }
</script>

<style scoped>

</style>



