<template>
  <v-card height="100%">
    <v-card-title>
      <span class="headline">修改个人信息</span>
    </v-card-title>
    <v-card-text>
      <v-form :model="admin">
        <v-container>
          <v-layout row>
            <v-flex xs6>
              <v-text-field v-model="admin.name" label="用户名" prepend-icon="account_box" required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs6>
              <v-text-field v-model="admin.actualName" label="真实姓名" prepend-icon="perm_identity" required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs6>
              <v-text-field v-model="admin.key" label="密码" prepend-icon="lock" type="password" required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs5>
              <v-radio-group v-model.number="admin.sex" row prepend-icon="supervisor_account">
                <v-radio label="男性" v-bind:value=0></v-radio>
                <v-radio label="女性" v-bind:value=1></v-radio>
              </v-radio-group>
            </v-flex>
          </v-layout>
        </v-container>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-layout row>
        <v-spacer></v-spacer>
        <v-flex xs8>
          <v-btn flat color="deep-orange darken-4" @click="save">保存</v-btn>
          <v-btn flat color="amber" @click="getInfo">重置</v-btn>
        </v-flex>
      </v-layout>
    </v-card-actions>
  </v-card>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "AdminInfo",
    data() {
      return {
        adminId: sessionStorage.getItem("id"),
        admin: {
          name: '',
          actualName: '',
          sex: 0,
          key: ''
        }
      }
    },
    methods: {
      getInfo() {
        this.$http.get('/admin/'+this.adminId)
          .then((response) => {
            this.admin = response.data;
            delete this.admin.auth;
          });
      },
      save() {
        this.$http.put('/admin/'+this.adminId, this.admin)
          .then((response) => {
            bus.$emit('show-info', '修改成功');
          })
          .catch((error) => {
            bus.$emit('show-info', '修改失败');
          })
      }
    },
    created() {
      this.getInfo();
    }
  }
</script>

<style scoped>

</style>



