<template>
  <v-layout row>
    <v-flex xs10 offset-xs1>
      <v-card>
        <v-card-title>
          <span class="headline">发起者列表</span>
          <v-spacer></v-spacer>
        </v-card-title>
        <v-data-table :headers="headers" :items="items" item-key="id" hide-actions>
          <template slot="items" slot-scope="props">
            <tr>
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.name }}</td>
              <td>{{ props.item.birth }}</td>
              <td>{{ getSex(props.item.sex)}}</td>
              <td>{{ props.item.email }}</td>
              <td class="layout px-0">
                <v-layout row justify-space-around>
                  <v-flex xs-1>
                    <v-btn icon class="mx-0" @click="openDialog(props.item.id)">
                      <v-icon color="teal">edit</v-icon>
                    </v-btn>
                  </v-flex>
                </v-layout>
              </td>
            </tr>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">
              没有找到发起者的数据 :(
            </v-alert>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
    <v-dialog v-model="editDialog" max-width="500" fullscreen scrollable hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar card dark color="primary">
          <v-btn icon dark @click.native="editDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>修改发起者信息</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark flat @click.native="submitItem">保存</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-form :model="requester">
            <v-container fluid>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="requester.name" label="用户名" prepend-icon="account_box" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="requester.key" label="密码" prepend-icon="lock" type="password" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-radio-group v-model.number="requester.sex" row prepend-icon="supervisor_account">
                    <v-radio label="男性" v-bind:value=0></v-radio>
                    <v-radio label="女性" v-bind:value=1></v-radio>
                  </v-radio-group>
                </v-flex>
              </v-layout>
              <v-layout row justify-space-between>
                <v-flex xs5>
                  <v-menu ref="menu" :close-on-content-click="false" v-model="dateMenu" :nudge-right="40" lazy
                          transition="scale-transition" offset-y full-width min-width="290px">
                    <v-text-field slot="activator" v-model="requester.birth" label="生日" prepend-icon="event" readonly required></v-text-field>
                    <v-date-picker ref="picker" v-model="requester.birth" :max="new Date().toISOString().substr(0, 10)"
                                   min="1950-01-01" @change="save(requester.birth)"></v-date-picker>
                  </v-menu>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="requester.email" label="邮箱" prepend-icon="email" type="email" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="requester.occupation" prepend-icon="turned_in" label="职务"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-text-field v-model="requester.company" prepend-icon="card_travel" label="公司"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import bus from '@/eventBus'
    export default {
      name: "RequesterTable",
      watch: {
        dateMenu (val) {
          val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'))
        }
      },
      data() {
        return {
          editDialog: false,
          dateMenu: false,
          headers: [
            {text: '编号', sortable: true, value: 'id'},
            {text: '用户名', sortable: true, value: 'name'},
            {text: '生日', sortable: false, value: 'birth'},
            {text: '性别', sortable: false, value: 'sex'},
            {text: '邮箱', sortable: false, value: 'email'},
            {text: '操作', sortable: false, value: ''},
          ],
          items: [],
          requesterId: 0,
          requester: {
            id: 0,
            name: '',
            key: '',
            birth: '',
            sex: '',
            email: '',
            occupation: '',
            company: ''
          }
        }
      },
      methods: {
        getSex: function(sex) {
          return sex === 0 ? '男' : '女';
        },
        save(date) {
          this.$refs.menu.save(date)
        },
        updateTable() {
          this.$http.get('/requester?type=checked')
            .then((response) => {
              this.items = response.data;
            })
        },
        openDialog(id) {
          this.$http.get('/requester/' + id)
            .then((response) => {
              this.requester = response.data;
              this.editDialog = true;
            })
            .catch((error) => {
              bus.$emit('show-info', error.data)
            })
        },
        submitItem() {
          const id = this.requester.id;
          delete this.requester.id;
          delete this.requester.state;
          this.$http.put('/requester/' + id, this.requester)
            .then((response) => {
              this.updateTable();
              this.editDialog = false;
              bus.$emit('sho-info', this.requester.name + "发起者修改成功");
            })
            .catch((error) => {
              bus.$emit('show-info', error.data)
            })
        }
      },
      created() {
        this.updateTable();
      }
    }
</script>

<style scoped>

</style>



