<template>
  <v-layout row>
    <v-flex xs12>
      <v-card>
        <v-card-title>
          <span class="headline">{{task.name}}</span>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" to="/worker/index">返回</v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <span class="subheading">{{task.description}}</span>
        </v-card-text>
        <v-container fluid>
          <v-layout row justify-space-between>
            <v-flex xs8>
              <v-app>
                <v-toolbar color="indigo darken-3" height="35">
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="getPrePic" :disabled="preButton">上一张</v-btn>
                  </v-toolbar-items>
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="getNextPic" :disabled="nextButton">下一张</v-btn>
                  </v-toolbar-items>
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="deleteSquare" :disabled="deleteButton">删除</v-btn>
                  </v-toolbar-items>
                </v-toolbar>
                <v-content>
                  <div id="imgStage" style="position:relative; overflow:auto"></div>
                </v-content>
              </v-app>
            </v-flex>
            <v-flex xs3>
              <v-form>
                <v-layout row v-for="label in task.labels" :key="label.title">
                  <v-text-field v-model="answer[label.title]" :label="label.title" v-if="label.choices.length === 0" required></v-text-field>
                  <v-select v-model="answer[label.title]" :items="label.choices" :label="label.title" required v-else></v-select>
                </v-layout>
                <v-layout row v-if="task.hasWholeLabel">
                  <v-text-field v-model="description" label="整体描述" required></v-text-field>
                </v-layout>
              </v-form>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  import Kinetic from 'kinetic'
  export default {
    name: "MultiMark",
    props: ['props'],
    data() {
      return {
        task: {},
        picOrderArray: [],
        preButton: false,
        nextButton: false,
        deleteButton: true,
        picOrder: 0,
        stage: {},
        layer: {},
        imageObj: new Image(),
        square: null,//等待保存的矩形
        answer: {},
        answerArray: [],
        description: '',
        descriptionArray: [],
      }
    },
    methods: {
      picPath() {
        if (this.task !== null && this.picOrderArray !== null)
          return 'http://120.79.145.97/image/' + this.task.id + '/' + this.picOrderArray[this.picOrder] + '.png'
      },
      getPostData() {
        let squareArray = this.stage.get(".square");
        if (this.answerArray.length <  squareArray.length) {
          this.answerArray.push(Object.assign({}, this.answer));
          this.descriptionArray.push(this.description);
        }
        let data = [];
        for (let i = 0; i < squareArray.length; i++) {
          let x = squareArray[i].getX();
          let y = squareArray[i].getY();
          let h = squareArray[i].getHeight();
          let w = squareArray[i].getWidth();
          //let labels = {};
          let square = {
            x: w < 0 ? x + w : x,
            y: h < 0 ? y + h : y,
            w: Math.abs(w),
            h: Math.abs(h),
            labelData: this.answerArray[i]
          };
          if (this.task.hasWholeLabel) square.description = this.descriptionArray[i];
          data.push(square);
        }
        return data;
      },
      update() {
        this.preButton = (this.picOrder === 0);
        this.nextButton = (this.picOrder === this.picOrderArray.length - 1);
        this.imageObj.src = this.picPath();
        this.stage.setWidth(this.imageObj.width);
        this.stage.setHeight(this.imageObj.height);
        this.layer.remove();
        this.layer = new Kinetic.Layer({id: "square"});
        this.stage.add(this.layer);
        this.answer = {};
        this.answerArray = [];
        this.description = '';
        this.descriptionArray = [];
        this.$http.get('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder])
          .then((response) => {
            let data = response.data;
            for (let i = 0; i < data.length; i++) {
              let square =  new Kinetic.Rect({
                name: 'square',
                id: i,
                x: data[i].x,
                y: data[i].y,
                height: data[i].h,
                width: data[i].w,
                stroke: 'red',
                strokeWidth: 3
              });
              this.layer.add(square);
              square.on('click', this.chooseEvent);
              this.answerArray.push(data[i].labelData);
              if (this.task.hasWholeLabel) this.descriptionArray.push(data[i].description);
            }
            this.stage.draw();
          });
      },
      getPrePic() {
        this.$http.post('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder], this.getPostData())
          .then((response) => {
            this.picOrder --;
            this.update();
          })
      },
      getNextPic() {
        this.$http.post('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder], this.getPostData())
          .then((response) => {
            this.picOrder ++;
            this.update();
          })
      },
      deleteSquare() {
        let squareArray = this.stage.get(".square");
        for (let i = 0; i < squareArray.length; i++) {
          if (squareArray[i].getStroke() === 'white') {
            squareArray[i].remove();
            this.layer.draw();
            this.answerArray.splice(i, 1);
            if (this.task.hasWholeLabel) this.descriptionArray.splice(i, 1);
            break;
          }
        }
        this.answer = {};
        this.deleteButton = true;
      },
      clickEvent(event) {
        let squareArray = this.stage.get(".square");
        if (this.square === null) {
          this.deleteButton = true;
          for (let i = 0; i < squareArray.length; i++) {
            squareArray[i].setStroke('red');
          }
          this.stage.draw();
          if (this.answerArray.length < squareArray.length) {
            this.answerArray.push(Object.assign({}, this.answer));
            this.descriptionArray.push(Object.assign({}, this.description));
          }
          this.answer = {};
          this.description = '';
          this.square = new Kinetic.Rect({
            name: 'square',
            id: squareArray.length,
            x: event.evt.offsetX,
            y: event.evt.offsetY,
            stroke: 'red',
            strokeWidth: 3
          });
          this.layer.add(this.square);
        } else {
          this.square.setHeight(event.evt.offsetY - this.square.getY());
          this.square.setWidth(event.evt.offsetX - this.square.getX());
          this.layer.draw();
          this.square.on('click', this.chooseEvent);
          this.square = null;
        }
      },
      chooseEvent(evt) {
        this.deleteButton = false;
        let squareArray = this.stage.get(".square");
        let sqaure = evt.target;
        if (this.answerArray.length < squareArray.length) {
          this.answerArray.push(Object.assign({}, this.answer));
          this.descriptionArray.push(Object.assign({}, this.description));
          this.answer = {};
          this.descriptionArray = '';
        }
        for (let i = 0; i < squareArray.length; i++) {
          squareArray[i].setStroke('red');
          squareArray[i].draw();
        }
        sqaure.setStroke('white');
        sqaure.draw();
        this.answer = this.answerArray[sqaure.getId()];
        this.description = this.descriptionArray[sqaure.getId()];
        evt.cancelBubble = true;
      },
      moveEvent(event) {
        if (this.square !== null) {
          this.square.setHeight(event.evt.offsetY - this.square.getY());
          this.square.setWidth(event.evt.offsetX - this.square.getX());
          this.layer.draw();
        }
      },
    },
    created() {
      this.$http.get('/task/' + this.$route.params.id)
        .then((response) => {
          this.task = response.data;
        });
      this.$http.get('/task/' + this.$route.params.id + '/picOrder')
        .then((response) => {
          this.picOrderArray = response.data;

          this.stage = new Kinetic.Stage({
            container: "imgStage", // div的id。同时也可以对这个div进行样式的添加。
            width: 1400,
            height: 500,
          });
          this.layer = new Kinetic.Layer({
            id: "square"
          });
          let layer = new Kinetic.Layer({
            id: "pic"
          });
          let yoda = new Kinetic.Image({
            x: 0,
            y: 0,
            image: this.imageObj,
          });
          this.stage.on("click", this.clickEvent);
          this.stage.on("mousemove", this.moveEvent);
          layer.add(yoda);
          this.stage.add(layer);
          this.stage.add(this.layer);
          this.stage.draw();

          this.update();
        });
    },
    mounted() {
    }
  }
</script>

<style scoped>

</style>



