<template>
  <v-layout row>
    <v-flex xs12>
      <v-card>
        <v-card-title>
          <span class="headline">{{task.name}}</span>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" to="/worker/index">返回</v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <span class="subheading">{{task.description}}</span>
        </v-card-text>
        <v-container fluid>
          <v-layout row justify-space-between>
            <v-flex xs8>
              <v-app>
                <v-toolbar color="indigo darken-3" height="35">
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="getPrePic" :disabled="preButton">上一张</v-btn>
                  </v-toolbar-items>
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="getNextPic" :disabled="nextButton">下一张</v-btn>
                  </v-toolbar-items>
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="deleteSquare" :disabled="deleteButton">删除</v-btn>
                  </v-toolbar-items>
                  <v-toolbar-items>
                    <v-btn dark flat @click.native="deleteSquare" :disabled="deleteButton">保存</v-btn>
                  </v-toolbar-items>
                </v-toolbar>
                <v-content>
                  <div id="imgdiv">
                    <canvas id="background" :height="imageObj.height" :width="imageObj.width">
                      抱歉，您的浏览器不支持画板功能
                    </canvas>
                    <img id="picShow" :src="picPath()" ondragstart="return false;">
                  </div>
                </v-content>
              </v-app>
            </v-flex>
            <v-flex xs3>
              <v-form>
                <v-layout row v-for="label in task.labels" :key="label.title">
                  <v-text-field v-model="answer[label.title]" :label="label.title" v-if="label.choices.length === 0" required></v-text-field>
                  <v-select v-model="answer[label.title]" :items="label.choices" :label="label.title" required v-else></v-select>
                </v-layout>
                <v-layout row v-if="task.hasWholeLabel">
                  <v-text-field v-model="description" label="整体描述" required></v-text-field>
                </v-layout>
              </v-form>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  class Point {
    constructor(x, y) {
      this.x = x;
      this.y = y;
    }
  }

  class Path {

    constructor(points) {
      this.points = points;
      this.k = undefined;
    }

    append(p) {
      if(this.points.length === 0) {
        this.points.push(p);
      } else if(this.points.length === 1) {
        let lastP = this.points[0];
        this.k = (p.y - lastP.y) / (p.x - lastP.x);
        this.points.push(p);
      } else {
        let lastP = this.points[this.points.length - 1];
        let k = (p.y - lastP.y) / (p.x - lastP.x);
        if(this.k === k) {
          this.points.pop();
          this.points.push(p);
        } else {
          this.points.push(p);
          this.k = k;
        }
      }
    }
  }

  class CanvasDrawer {

    constructor(background) {
      this.background = background;
      this.isDrawing = false;
      this.paths = null;
      this.cxt = this.background.getContext('2d');
      this.cxt.clearRect(0,0,this.background.width, this.background.height);
      this.cxt.strokeStyle = 'black';
      this.cxt.lineWidth = 2;
      this.background.addEventListener('mousedown', e => this.startDraw(e));
      this.background.addEventListener('mousemove', e => this.drawing(e));
      this.background.addEventListener('mouseup', e => this.endDraw(e));
    }

    // 该函数按照paths数组中每条路径的顺序依次生成一个canvas并描绘一条路径
    // 不检查该图层是否被隐藏
    // 用于开始时将之前的路径加载出来
    drawPaths() {
      let last = this.paths.points.length - 1;
      this.cxt.beginPath();
      this.cxt.moveTo(this.paths.points[last].x, this.paths.points[last].y);
      for(let p of this.paths.points) {
        this.cxt.lineTo(p.x, p.y);
        this.cxt.stroke();
      }
      this.cxt.closePath();
    }

    startDraw(e) {
      if(this.isDrawing) return;
      this.isDrawing = true;
      let border = this.background.getBoundingClientRect();
      let x = e.clientX - parseInt(border.left);
      let y = e.clientY - parseInt(border.top);
      this.paths = new Path([new Point(x, y)]);
      this.cxt.beginPath();
      this.cxt.moveTo(x, y);
    }

    drawing(e) {
      if(!this.isDrawing) return;
      let border = this.background.getBoundingClientRect();
      let x = e.clientX - parseInt(border.left);
      let y = e.clientY - parseInt(border.top);
      this.cxt.lineTo(x, y);
      this.cxt.stroke();
      this.paths.append(new Point(x, y));
    }

    endDraw(e) {
      if(!this.isDrawing) return;
      this.isDrawing = false;

      if(this.paths.points.length < 2) {
        this.cxt.closePath();
        return;
      }
      // close this path
      //this.cxt.lineTo(this.startX, this.startY);
      this.cxt.closePath();
      this.cxt.stroke();
    }

    output() {
      for(let i = 0; i < this.paths.length; i++) {
        console.log('--------------------------');
        let p = this.paths[i];
        for(let e of p.points) {
          console.log(e.x + ', ' + e.y);
        }
      }
    }
  }
  export default {
    name: "AreaMark",
    data() {
      return {
        task: {},
        picOrderArray: [],
        preButton: false,
        nextButton: false,
        deleteButton: true,
        picOrder: 0,
        imageObj: new Image(),
        drawer: {},
        answer: {},
        description: '',
      }
    },
    methods: {
      picPath() {
        if (this.task !== null && this.picOrderArray !== null) {
          let src = 'http://120.79.145.97/image/' + this.task.id + '/' + this.picOrderArray[this.picOrder] + '.png';
          this.imageObj.src = src;
          return src;
        }
      },
      update() {
        this.preButton = (this.picOrder === 0);
        this.nextButton = (this.picOrder === this.picOrderArray.length - 1);
        this.imageObj.src = 'http://120.79.145.97/image/' + this.task.id + '/' + this.picOrderArray[this.picOrder] + '.png';
        this.answer = {};
        this.description = '';
        this.drawer = new CanvasDrawer(background);
      },
      getPostData() {
        this.drawer.output();
        return this.drawer.paths;
      },
      getPrePic() {
        this.$http.post('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder], this.getPostData())
          .then((response) => {
            this.picOrder --;
            this.update();
          })
      },
      getNextPic() {
        this.$http.post('/square/'+this.task.id+"/"+this.picOrderArray[this.picOrder], this.getPostData())
          .then((response) => {
            this.picOrder ++;
            this.update();
          })
      },
    },
    created() {
      this.$http.get('/task/' + this.$route.params.id)
        .then((response) => {
          this.task = response.data;
        });
      this.$http.get('/task/' + this.$route.params.id + '/picOrder')
        .then((response) => {
          this.picOrderArray = response.data;
          this.update();
        });
    },
  }
</script>

<style scoped>
  #imgdiv{
    position: relative;
    overflow:auto;
    margin: 0 auto;
  }
  #background {
    position: absolute;
    left: 0px;
    top: 0px;
  }
</style>



